@import "color.scss";

.ck-editor {
    border: none;

    /*
    &__editable {
        background: $white !important;
    }
    */

    pre, code, p, h1, h2, h3 {
        color: black !important;
    }

    .image, .image_resized {
        max-width: 50% !important;
    }

}

.ck-content {
    border: solid 1px $grey !important;
}
.ck-focused {
    border: solid 1px $grey !important;
    box-shadow: none !important;
}