@import "color.scss";

.createtopic {
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    & > div {
        width: 100%;
    }

    &-content {
        width: 100%;
        display: flex;
        flex-direction: column;
        h2 {
            margin: 20px 0;
            text-align: center;
            color: $main;
            font-size: 28px;
        }
    }

    &-preview {
        padding-top: 20px;
        width: 100%;

        height: 100%;

    }
    .ck-rounded-corners {
        box-shadow: 0 2px 20px rgb(0 0 0 / 3%);

    }

    &-input {
        display: grid;
        grid-template-columns: 1fr 140px 140px;
        margin-bottom: 20px;
        box-shadow: 0 2px 20px rgb(0 0 0 / 10%);

        input {
            border: solid 1px $grey;
            height: 35px;
            padding: 0 10px;
            font-size: 18px;
        }

        select {
            border: solid 1px $grey;
            border-left: none;
            font-size: 16px;
            color: $main;
            font-weight: 600;

            &:disabled {
                color: $grey;
            }
        }
    }

    &-footer {
        display: flex;
        padding-bottom: 30px;
        margin-top: 10px;
        gap: 20px;
        justify-content: center;

        .btn {
            font-size: 18px;
        }
    }
}