@import "color.scss";

.home {
    display: flex;
    flex-direction: column;
    justify-content: space-around;


    &-container {

        &-title {
            display: flex;
            justify-content: space-between;
            align-items: center;

            h2 {
                font-weight: 450;
            }
        }
    }

    .favorite {
        display: flex;
        gap: 40px;
        justify-content: flex-start;
        margin-bottom: 20px;
    }

    .recenttopic {
        display: flex;
        height: calc(100% - 100px);
        gap: 40px;
        align-items: stretch;
    }
}

.box {
    background-color: $white;
    backdrop-filter: blur(2px);
    border: solid 1px $grey;
}

.card {
    background-color: $white;
    border-radius: 25px;

    box-shadow: 0 2px 20px rgb(0 0 0 / 10%);
    padding: 18px;
    cursor: pointer;
    transition: .2s ease-in-out;

    


    &:hover {
        box-shadow: 0 2px 20px rgb(0 0 0 / 25%);
    }

    &--simple {
        box-shadow: 0 2px 12px rgb(0 0 0 / 14%);

        &:hover {
            box-shadow: 0 2px 12px rgb(0 0 0 / 30%);
        }
    }

}

h2 {
    font-weight: 550;
}

@media screen and (max-width: 1070px) and (min-width: 1000px) {

    .recenttopic{
        & > div:nth-child(2) {
            display: none;
        }
    }
}

@media screen and (max-width: 1230px) and (min-width: 1000px) {

    .recenttopic{
        & > div:nth-child(3) {
            display: none;
        }
    }
}

@media screen and (max-height: 770px) and (min-width: 1000px) {

    .recenttopic {
       display: none !important;
    }

    .home {
        &-container {
            height: fit-content !important;
        } 
    }
}