@import "color.scss";

.document {

    &-upload {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;


        &-container {
            display: flex;
            flex-direction: column;
            gap: 10px;

            p {
                text-align: center;
                color: grey;
            }


        }

        p {
            margin: 0;
            max-width: 300px;
            overflow: hidden;
            text-overflow: ellipsis;
            height: 20px;
        }
    }
    
    &-upload-dropzone {
        border: 2px solid $main;
        width: 300px;
        height: 100px;
    }

    &-upload {
        margin: 15px;
        width: calc(100% - 30px);
        height: calc(100% - 30px);
        border: solid 1px $grey;
        box-sizing: border-box;
    }

    .document-upload {
        input {
            width: 300px;
            padding: 10px;
            border: solid 1px grey;
        }
    
        select{
            width: 320px;
            padding: 5px;
            border: solid 1px grey;
        }
    
        input[type="file"] {
            width: 320px;
            padding: 10px;
            border: solid 1px grey;
            box-sizing: border-box;
        }
    }



}


.drag-container {
    border: dashed 3px $main;
    padding: 40px 20px;
    color: $main;
    font-weight: 700;
    p {
        text-align: center;
        color: $main;
    }

    .active {
        color: $main;
    }
}

.overlay {
    border: dashed 3px rgb(0, 195, 0) !important;
    color: rgb(0, 195, 0) !important;
}