@import "color.scss";

.topicmessage {
    padding: 0;

    

    &-head {
        background-color: $back2;
        height: 60px;
        border-bottom: solid 1px $grey;
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        padding: 0 20px;

        h2 {
            margin: 0;
            font-size: 22px;
            color: $main;

            &::first-letter{ 
                text-transform: capitalize;
            }
        }

        span {
            display: flex;
            align-items: center;
            gap: 15px;
        }
    }

    &-overflow {
        overflow-y: auto;
        height: calc(100vh - 60px);
    }
    &-wrapper {
        margin: 20px;
        display: flex;
        flex-direction: column;
        
        gap: 10px;
        min-height: calc(100% - 100px);
        height: fit-content;

    }

    &-edit {
        padding: 20px;
        display: flex;
        flex-direction: column;



        span {
            align-self: center;
            margin-top: 20px;
            display: flex;
            gap: 20px;
        }
    }


}