@import "color.scss";

.categories {
    height: 100vh;
    width: 100%;
    padding: 0;
    box-sizing: border-box;

    &-vote {
        display: flex;
        flex-direction: column;
        gap: 15px;
        padding: 15px 0;
        border: solid 1px $grey;
        background-color: white;
        border-radius: 15px;

        margin-top: 15px;
        margin-bottom: 5px;
        label {
            text-align: center;
        }

        span {
            display: grid;
            gap: 20px;
            grid-template-columns: 50px 1fr;
            padding: 0 15px;

            input {
                border: solid 1px grey;
            }
        }
    }

    &-head {
        background-color: $back2;
        min-height: 60px;
        height: fit-content;
        width: 100%;
        border-bottom: solid 1px $grey;

        display: flex;
        justify-content: space-between;
        align-items: center;


        svg {
            font-size: 40px;
            color: #ffc928;
            padding-right: 10px;
            cursor: pointer;
        }

        &-path {
            height: 100%;
            display: flex;
            overflow-x: hidden;
            padding: 10px;
            align-items: center;
            gap: 10px;
            flex-wrap: wrap;
        }

    }

    &-section {
        height: 100%;
        display: flex;
        width: 100%;
    }
    
    &-navigation {
        padding: 20px;
        margin-top: 10px;
        box-sizing: border-box;

        display: flex;
        flex-flow: row wrap;
        overflow-y: auto;
        margin-right: 10px;
        justify-content: flex-start;
        
        gap: 40px;
        max-width: 100%;
        max-height: calc(100% - 80px);



    }
    
    &-aside {
        display: flex;
        flex-direction: column;
        align-self: center;
        justify-content: center;

        &-mark {
            background-color: $white;
            margin-top: 20px;
            border: solid 1px $grey;
            padding: 10px;
            border-radius: 25px;
        }
        .btn {
            align-self: center;
            margin-top: 20px;

            width: 180px;
        }
    }
}


@media screen and (max-width: 1000px) {
    .categories {
        &-navigation {
            height: calc(100vh - 160px);
        }
    }
}