@import "color.scss";

.centerpage {
    width: 100vw;
    height: 100vh;
    background: $back;
    display: flex;
    justify-content: center;
    align-items: center;

    &-main {
        background-color: $white;
        padding: 40px;

        min-height: 300px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-evenly;
        flex-direction: column;
        align-items: center;

        form {
            display: flex;
            justify-content: center;
            gap: 20px;
            flex-direction: column;
            align-items: center;

            input {
                width: 100%;
                margin: 30px 0;
                border: solid 1px $grey;
                padding: 7px;
            }
        }
    }
}

