@import "color.scss";

.header {
    height: 100%;
    min-width: 90px;
    width: 90px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-right: solid 1px $grey;
    background-color: $back2;
    transition: .2s ease-in-out;

    .container {
        width: 100%;

        &-main {
            display: flex;
            flex-direction: column;
            align-items: center;

            .link {
                overflow: visible !important;
                margin: 20px 0;
                width: 100%;

                svg, p {
                    color: $grey;
                    overflow: visible !important;

                    &:hover {
                        color: $main;
                    }
                }

                &--selected {
                    border-right: solid 1px $main;

                    svg, p {
                        color: $main;
                    }
                }

                a {            
                    width: 100%;
            
                    display: flex;
                    margin-left: 25px;
                    text-decoration: none;

                    display: flex;
                    align-items: center;
                    font-size: 35px;

                    p {
                        visibility: hidden;
                        opacity: 0;
                        font-size: 15px;
                        margin: 0;
                        padding: 0;
                        margin-left: 20px;
                    }
                }
            }
        }

        &-icon {
            display: flex;
            justify-content: center;
            
            img {
                width: 60px;
                margin-top: 15px;
            }
        }
    }


}

@media screen and (max-width: 1000px) {
    .header {
        flex-direction: row;
        width: 100vw;
        box-sizing: border-box;
        overflow-x: hidden;
        height: 78px;
        
        position: fixed;
        top: calc(100vh - 78px);
        left: 0;
        z-index: 10;
        padding-right: 20px;
        border-top: solid 1px $grey;

        .link--selected {
            border: none !important;
        }

        .container {
            flex-direction: row;
            justify-content: space-around;
            box-sizing: border-box;
            &-icon {
                display: none;
            }

            &:nth-child(3) {
                width: fit-content  !important;
                
            }
            
        }
    }


  }