@import "color.scss";

.chat {
    height: calc(100vh - 60px);
    box-sizing: border-box;
    
    display: grid;
    grid-template-rows: 10fr 1fr;
    margin-left: 20px;

    &-messages {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;


        width: 100%;
        max-width: 100%;
        height: calc(100% - 20px);
        padding-right: 10px;
        overflow-y: auto;

        margin-top: 5px;
        gap: 10px;
    }

    form {
        width: 100%;
        height: 80px;
        display: flex;
        box-sizing: border-box;
        
        padding-right: 10px;
        

        textarea {
            width: 100%;
            border: none;
            border-radius: 10px;
            resize: none;
            box-sizing: border-box;
            padding: 8px;
            font-size: 16px;
            border: solid 1px $grey;
            //box-shadow: rgba(100, 100, 111, 0.103) 0px 7px 29px 0px;
        }

        div {

            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: 20px;
            
            button {
                width: 50px;
                height: 50px;
                margin: auto;
            }
        }

    }



}