@import "color.scss";

.calendarsearch {
    display: grid;
    grid-template-rows: 250px 1fr;
    grid-template-columns: 1fr;
    gap: 20px;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    box-sizing: border-box;

    .box {
        width: 100%;
        margin: auto;
        box-sizing: border-box;
        height: 100%;
    }
    
    form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 20px;
        height: 100%;
        box-sizing: border-box;

        gap: 40px;
        label {
            font-size: 18px;
        }

        span {
            display: flex;
            gap: 10px;
            align-items: center;
        }

        input, select {
            border: solid 1px grey;
            padding: 4px;
        }
    }

    .rooms {
        padding: 20px;
        gap: 20px;
        display: flex;
        flex-wrap: wrap;
        max-width: 100%;
        overflow-y: auto;
        width: 100%;
        height: 100%;
        div {
            border: solid 1px $grey;
            width: 80px;
            height: 80px;

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            padding: 10px;
        }
    }

    &-mini {
        display: flex;

        .box {
            width: fit-content;
            height: fit-content;
        }
    }
}