@import "color.scss";

p, h1, h2, h3, svg, label, input, select, div, ul, li {
  color: $black;
}

input, select, textarea {
	outline: none;
	background-color: $white;
}

label {
	cursor: pointer;
}
body {
  margin: 0;
  overflow: hidden;
}

*::-webkit-scrollbar-track
{
	//box-shadow: inset 0 0 6px rgba(255, 255, 255, 0.123);
	border-radius: 10px;
	background-color: $grey; // #e2e2e2;
}

*::-webkit-scrollbar
{
	width: 7px;
	height: 0px;

}

*::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.13);
	background-color: $main;
}
// --------

.btn {
    background-color: $main;
    border-radius: 50px;
    color: white;
    text-decoration: none;
    border: none;
    padding: 6px 13px;
    font-size: 16px;
	display: flex;
	align-items: center;
	justify-content: center;
    cursor: pointer;
    transition: .2s ease-in-out;
	font-weight: 700;
	text-align: center;
	box-shadow: rgba(63, 72, 82, 0.15) 4px 4px 7px;


	p,svg {
		color: white;
		margin: 0 5px;
	}

	svg {
		font-size: 22px;
	}
	border: solid 2px rgba(207, 207, 207, 0);

	&:hover {
		background-color: $white;
		color: $main !important;
		border: solid 2px $main;

		p, svg {
			color: $main !important;
		}
	}

    &-grey {
		$color: rgb(194, 194, 194);
        background-color: $color;
		&:hover {
			color: $color !important;
			border-color: $color !important;

			svg {
				color: $color !important;
			}
		}
    }

	&-circle {

			width: 40px;
			padding: 3px 0;
			svg {
				font-size: 50%;
				font-size: 23px;
				padding: 3px;
			}
	
	}

	&-reverse {
        background-color: $white;
		color: $main !important;
		border-color: $main !important;

		svg {
			color: $main !important;
		}

		&:hover {
			background-color: $main !important;
			color: $white !important;

			svg {
				color: $white !important;
			}
		}
    }

    &-gold {
        $color: #e7c965;
		background-color: $color;
		&:hover {
			color: $color !important;
			border-color: $color !important;
		}
    }


	&-lightgrey {
        $color: #d3d7da;
		background-color: $color;
		&:hover {
			color: $color !important;
			border-color: $color !important;
		}
    }

	&-icon {
		border-radius: 50%;
		font-size: 22px !important;
		padding: 5px;

		svg {
			margin: 0;
		}
	}

    &:disabled, &-red {
		$color: rgb(255, 62, 62);
        background-color: $color;


		&:hover {
			animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
			transform: translate3d(0, 0, 0);
			perspective: 1000px;
		  }
		  
		  @keyframes shake {
			10%, 90% {
			  transform: translate3d(-1px, 0, 0);
			}
			20%, 80% {
			  transform: translate3d(2px, 0, 0);
			}
			30%, 50%, 70% {
			  transform: translate3d(-4px, 0, 0);
			}
			40%, 60% {
			  transform: translate3d(4px, 0, 0);
			}
		  }

		&:hover {
			color: $color !important;
			border-color: $color !important;
			background-color: rgba(0, 0, 0, 0);
			
			p, svg {
				color: $color !important;
			}
		}
    }

}

