.app {
  width: 100vw;
  height: 100vh;

  margin: 0;
  padding: 0;

  display: flex;
  align-items: center;

  overflow: hidden;
}

.page {
  width: 100%;
  height: 100%;
  padding: 25px;
  max-width: 100%;
  overflow: hidden;

  background: $back;
  box-sizing: border-box !important;


}

.pagetest {
  background: -webkit-gradient(linear,left top,right top,from(#9fd1f344),to(#b8f7e436));
  background: linear-gradient(90deg, #aea4e362, #d3ffe865);
  background: linear-gradient(90deg, #89add669, #94f8ff8e);

background-color: #485461;
  background-image: linear-gradient(315deg, #485461 0%, #28313b 74%);

  background: linear-gradient(60deg, #28313b, #485461);

  background: linear-gradient(90deg, #faf0cd93, #fabb977a);
}

