.loading {
	padding: 0;
	margin: 0;
	width: 100vw;
	height: 100vh;
	background: $back;
	display: flex;
	align-items: center;
	justify-content: center;
}

.loadingOuter {
	position: relative;
	width: 256px;
	height: 10px;
	background: $back2;
  border-radius: 5px;
  border: solid 1px #ddd;
}

.loadingInner {
	position: absolute;
	width: 0px;
	height: 10px;
	left: 0px;
	background: $main;
	animation: 2s infinite linear loadingAnimation;
  border-radius: 5px;
}

@keyframes loadingAnimation {
	0% {
		left: 0px;
		width: 0px;
	}
	
	25% {
		left: 0px;
		width: 100px;
	}
	
	70% {
		left: 200px;
		width: 56px;
	}
	
	90% {
		left: 256px;
		width: 0px;
	}
}

.limit {
	position: absolute;
	left: 0;
	right: 0;
	display: none;
	div {
		height: 100vh;
		width: 100vw;
	}
}

@media screen and (max-width: 1000px) {
    .app {
        flex-direction: column;
		height: fit-content !important;
		box-sizing: border-box;
		overflow-x: hidden;
    }

	.page {
		overflow-x: hidden;
		box-sizing: border-box;
		height: fit-content !important;
		min-height: 100vh;
		padding-bottom: 78px !important;
	}

	.limit {
		display: flex;
	}
	
  }

