.edit-profile{display:flex;justify-content:center;align-items:center}.edit-profile>div{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;width:90%;height:90%;background-color:var(--white);border:solid 1px var(--grey)}.edit-profile>div form{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;width:65%;height:80%}.edit-profile>div form .form-inputs{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr;width:100%;height:70%;grid-gap:20px;gap:20px}.edit-profile>div form .form-inputs div{border:solid 1px var(--grey);padding:6%;display:flex;flex-direction:column;justify-content:space-between}.edit-profile>div form .form-inputs div:nth-of-type(2n){align-items:flex-end}.edit-profile>div form .form-inputs div:nth-of-type(2n)>input{text-align:right}.edit-profile>div form .form-inputs div input{box-shadow:none;border:none;border-bottom:2px solid #adadad;background-color:var(--white);color:var(--black);transition:.2s ease-in-out;width:100%;font-size:medium}.edit-profile>div form .form-inputs div input:focus{outline:none;border-bottom:2px solid var(--main)}.edit-profile>div form .form-btn{display:flex;flex-direction:row;justify-content:space-between;width:60%}.edit-profile>div form .form-btn .btn{width:45%}
.mention .container{overflow-y:scroll;height:100%;padding-right:20px}.mention .container h1{color:var(--main)}.mention .container *{text-align:justify}

.register{display:flex;justify-content:center;align-items:center}.register-main{display:flex;justify-content:space-between;align-items:center;background-color:var(--white);width:52em;height:30em;border-radius:15px;box-shadow:rgba(0,0,0,.1) 0px 4px 12px;padding:20px;box-sizing:border-box}.register form{display:flex;flex-direction:column;justify-content:space-between;align-items:center;border-left:1px solid #d6d6d6;padding-left:3%;width:62%;height:100%}.register form h2{text-align:center}.register-input{display:flex;flex-direction:column;grid-gap:2px;gap:2px}.register-button{display:flex;grid-gap:20px;gap:20px}.register input{box-shadow:none;border:none;border-bottom:2px solid #adadad;background-color:var(--white);color:var(--black);transition:.2s ease-in-out;padding:5px 0;font-size:15px;width:300px}.register input:focus{outline:none;border-bottom:2px solid var(--main)}.register label{color:var(--main);margin-top:10px;font-size:12px}.register-figure{display:flex;flex-direction:column}.register-figure .line{width:3px;height:40px;background-color:#c7c7c7;margin-left:24px;transition:.35s ease-in-out;transition-delay:.15s}.register-figure .line-active{transition:.35s ease-in-out;transition-delay:0;background-color:var(--main)}.register-figure span{display:flex}.register-figure span .cirle{border-radius:50%;background-color:#c7c7c7;width:50px;height:50px;display:flex;justify-content:center;align-items:center;color:#fff;font-size:20px;transition:.35s ease-in-out;transition-delay:0}.register-figure span .active{background-color:var(--main);transition-delay:.15s}.register-figure span p{color:#c7c7c7;margin-left:10px;transition:.35s ease-in-out;text-transform:capitalize}.register-figure span .p-active{color:#5e5e5e}.register-recap{display:flex;justify-content:center;align-items:center;flex-direction:column;grid-gap:7px;gap:7px}.register-recap img{width:180px;height:180px;border-radius:50%;border:solid 1px #ddd;margin-bottom:15px}.register-recap p{margin:3px;text-transform:capitalize}.register-recap-mail{text-transform:lowercase !important;color:var(--main);font-size:14px}


.register form select{height:30px;margin:10px 0;border:none;border-bottom:solid 2px #adadad;transition:.2s ease-in-out}.register form select:focus{border-color:var(--main)}
.register-wrapper{display:flex;flex-wrap:wrap;grid-gap:5px;gap:5px}.register-wrapper .subject-button{background-color:#e0e0e0;color:#4b4b4b;padding:10px;border-radius:10px;cursor:pointer;transition:1s ease-in-out}.register-wrapper .subject-button-active{background-color:var(--main);color:#fff}.register-count{align-self:center;margin-top:30px;border:solid 2px var(--main);padding:5px 8px;border-radius:50px;font-weight:700;color:var(--main)}
.register-crop{position:absolute;top:0;left:0;width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;flex-direction:column;background-color:rgba(0,0,0,.616);-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px)}.register-crop-button{display:flex;grid-gap:20px;gap:20px;margin-top:20px}.ReactCrop__image{max-height:calc(80vh - 70px);max-width:80vw;background-color:#fff}.ReactCrop__crop-selection{border-radius:50%}.register-img{width:220px;height:220px;border-radius:50%;border:solid 1px #ddd}
.login{display:flex;align-items:center;flex-direction:column;justify-content:center}.login .error{color:red;font-size:18px}.login-laptop{width:25m;height:25em}.login-deux,.login-laptop{margin-right:auto;margin:2em}.login form{display:flex;flex-direction:row;background-color:var(--white);width:52em;height:30em;justify-content:space-around;border-radius:15px;box-shadow:rgba(0,0,0,.1) 0px 4px 12px}.login-mail{display:flex;flex-direction:column}.login-password{display:flex;flex-direction:column}.login-mail h2{font-weight:bold}.login input{box-shadow:none;border:none;border-bottom:2px solid #adadad;background-color:var(--white);color:var(--black);transition:.2s ease-in-out}.login input:focus{outline:none;border-bottom:2px solid var(--main)}.login input,.login button{height:3em;margin-top:2em}.login-arobase{display:flex;flex-direction:row;align-items:center}.login p{font-size:10pt;align-self:end;margin-left:1em}.login-link{margin-top:20px;display:flex;flex-direction:column;grid-gap:10px;gap:10px;font-size:14px}.login-keep{display:flex;align-items:center;cursor:pointor !important}.login-keep input{margin:0;margin-right:10px}
.centerpage{width:100vw;height:100vh;background:var(--back);display:flex;justify-content:center;align-items:center}.centerpage-main{background-color:var(--white);padding:40px;min-height:300px;box-sizing:border-box;display:flex;justify-content:space-evenly;flex-direction:column;align-items:center}.centerpage-main form{display:flex;justify-content:center;grid-gap:20px;gap:20px;flex-direction:column;align-items:center}.centerpage-main form input{width:100%;margin:30px 0;border:solid 1px var(--grey);padding:7px}
