@import "color.scss";

.addtopic {
  min-width: 450px;
  width: 40%;
  margin-top: 10px;
  height: 180px;
  display: flex;
  align-items: center;


  img {
    max-width: 200px;
    width: auto;
    height: auto;
    max-height: 100%;

  }

  &-text {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;

    h3 {
      width: 100%;
      text-align: center;
    }
    
    & > p {
      margin: 0%;
      padding-left: 10px;
      text-align: justify;

      color: grey;
    }

    &-btn {
      display: flex;
      align-items: center;
      background-color: $main;
      border-radius: 40px;
      padding: 10px 15px;
      text-decoration: none;

      svg {
        font-size: 20px;
        color: $white;
      }
      
      p {
        color: $white;
        padding-left: 4px;
        font-weight: 900;
        
      }
      transition: .2s ease-in-out;

      &:hover {
        transition: .2s ease-in-out;

        background-color: $main;

      }
    }
  }

}
