@import "color.scss";

.usericon {
    text-decoration: none !important;
    cursor: pointer;

    img {
        border-radius: 50%;
        margin-bottom: 5px;
        box-shadow: rgba(0, 0, 0, 0.116) 4px 12px 12px;
    }

    p {
        font-weight: 500;
        text-align: center;
        text-transform: capitalize;
        margin: 0 !important; 
    }

}