@import "color.scss";

.forum {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 0;
    
    &-head {
        background-color: $back2;
        height: 60px;
        border-bottom: solid 1px $grey;

        display: flex;
        align-items: center;
        padding: 11px 20px;
        box-sizing: border-box;
        justify-content: space-between;
    }

    table {
        display: block;
        
        margin: 20px;

        padding: 5px 10px;
        min-height: calc(100vh - 110px) !important;
        max-height: calc(100vh - 110px) !important;
        overflow-y: auto;
  
    }
    &-search {
        display: flex;
        align-items: center;
        

        svg {
            font-size: 30px;
            border-bottom: solid 2px $main;
            padding: 1px;
            color: $main;
        }

        input {
            width: 300px;
            height: 30px;
            border: none;
            border-bottom: solid 2px $main;
            background-color: rgba(0, 0, 0, 0);

            font-size: 18px;
            padding-left: 10px;
            color: $main;
        }
    }


}

.filter {
    form {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        gap: 10px;
    }

    form > label {
        color: $main;
        font-weight: 600;
    }

    &-date {
        display: flex;
        flex-direction: column;
        gap: 5px;
        span {
            display: flex;
            justify-content: space-between;
        }
        p {
            margin: 0;
            font-size: 14px;
            color: grey;
        }
    }

    &-status {
        display: flex;
        justify-content: space-between;
        height: 20px;

        label {
            color: grey;
        }
    }

    .btn {
        margin-top: 15px;
        width: fit-content;
        align-self: center;
    }

    input, select {
        border: solid 1px grey;
        height: 24px;
    }
}

@media screen and (max-width: 1000px) {
    .forum {


    table {
        margin: 0 !important;

        background-color: rgba(255, 0, 0, 0) !important;
        border: solid 1px $grey !important;
    }

    &-head {
        flex-wrap: wrap;
        min-height: 60px;
        height: fit-content;
    }
    &-search { 
        input {
            width: auto;
        }
    }

    }
}