@import "color.scss";

.edit-profile {

    display: flex;
    justify-content: center;
    align-items: center;

    & > div{

        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;

        width:90%;
        height: 90%;
        background-color: $white;
        border:solid 1px $grey;
        
        form{
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;
            width: 65%;
            height:80%;
       
            .form-inputs{
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 1fr 1fr;
                width:100%;
                height:70%;
              
                gap: 20px;
                div{
                    border:solid 1px $grey;
                    padding: 6%;
                    display: flex;
              
                    &:nth-of-type(2n){
                        align-items: flex-end;

                        & > input{
                            text-align: right;
                        }
                    }
                    flex-direction: column;
                    justify-content: space-between;
                    input{
                        box-shadow: none;
                        border:none;
                        border-bottom: 2px solid rgb(173, 173, 173);
                        background-color: $white ;
                        color : $black ;
                        transition: .2s ease-in-out;
                        width: 100%;
                        font-size: medium;

                        &:focus {
                            outline:none;
                            border-bottom: 2px solid $main;
                        }
                    }
                }
            }
            
            .form-btn{
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                width:60%;

                .btn{
                    width: 45%;
                }
            }

        }

    }

}