@import "color.scss";


.topic {
    display: grid;
    grid-template-columns: 40px 1fr 110px 160px 250px;
    gap: 10px;
    padding: 9px 10px;

    border-bottom: solid 1px $grey;


    td, span {
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        
    }

    span {
        gap: 5px;
    }
    &-category {
        justify-content: flex-end !important;
        p {
            font-size: 14px;
        }
    }

    &-icon {

        img {
            margin: 0 !important;
        }
    }

    &-status {

        justify-content: center !important;
        gap: 10px;
    }

    &-title {
        overflow: hidden;
        p {
            margin: 0;
            font-weight: 500;
            text-overflow: ellipsis;
            overflow: hidden;
            height: 20px;
        }

    }

    &-date {
        color: grey;
    }

    p::first-letter {
        text-transform: capitalize !important;
       
    }

    &:hover {

        background-color: rgba(0,0,0,0.05) !important;
    }
}

@media screen and (max-width: 1000px) {
    .topic {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        background-color: $white;
        border: solid 1px $grey;

        td {


            &:nth-child(2) {
                display: flex;
                p{
                    text-overflow: ellipsis;
                    overflow: hidden;
                    width: calc(100vw - 40px);
                }
            }

            &:nth-child(1), &:nth-child(4) {
                display: none;
            }
        }

    }


}