@import "color.scss";

.profil{

    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .profil-overview{
        width: 98%;

        height: 150px;
        overflow: hidden;

        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        .badges{
            svg{
                padding:15px;
                background-color: rgb(255, 216, 86);
                color:white;
                font-size: 25px;
                border-radius: 50px;
                margin: 3px;
            }
        }

        .profil-infos{
            margin: 0 40px;
            height: 35%;
            border-radius: 10px;
            display: flex;
            align-items: center;
            flex-direction: row;
            
            span{
                margin: 0 20px;

                p:nth-of-type(1){
                    font-size: 30px;
                    color: $main;
                    text-transform: capitalize;
                    font-weight: 700;
                    margin : 0;
                }

                p:nth-of-type(2){
                    margin : 0;
                    font-size: 25px;
                    font-weight: 400;
                    color: grey;
                }

                p:nth-of-type(3){
                    margin : 0;
                    font-size: 18px;
                    font-weight: 400;
                    color: grey;
                }
            }
        }

        .overview-btn{
            margin: 0 20px;
            justify-content: center;
            align-items: center;
            display: flex;
            gap: 10px;

            svg {
                font-size: 30px;
                height: 54px;

            }
        }   
    }

    .profil-datas{
        margin: 20px 0 0 0;
        display: flex;
        justify-content: space-between;
        width: 98%;
        height:76%;

        .nocontrib {
            width: 100%;
            text-align: center;
            font-size: 20px;
            color: grey;
            margin: 0;
        }

        .profil-stats{
            width:40%;
            text-align: center;
            display:flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-evenly;

            & > div{
                width:75%;
                height: calc(100% - 150px);
                display:grid;
                grid-template-columns: 1fr 1fr;
                gap: 15px;

                .stat-div{
                    padding: 10px;
                    border: solid 1px $grey;
                    border-radius: 10px;
                 
                    transition: 0.2s;
                    p{
                        margin:0;
                    }
                    p:nth-of-type(1){
                        color:rgb(175, 175, 175);
                        font-size: 14px;
                        overflow: hidden;
                        

                    }
                    p:nth-of-type(2){
                        font-size: 50px;
                        font-weight: bold;
                    }

                    &:hover{
                        background-color: rgb(243, 243, 243);
                    }
                }
                
                .stat-div:nth-of-type(n){
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    align-items: flex-start;
                    p:nth-of-type(1){
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        width: 100%;
                    }
                }
                .stat-div:nth-of-type(2n){
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    align-items: flex-end;
                    p:nth-of-type(1){
                        display: flex;
                        justify-content: space-between;
                        flex-direction: row-reverse;
                        align-items: center;
                        width: 100%;
                    }
                }
            }
        }

        h1{
            color: $main;
            margin: 0 0 15px 0;
            width: 75%;
            height: 10%;
            border-radius: 10px;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .profil-posts{
            
            width:58%;
            text-align: center;
            display:flex;
            flex-direction: column;
            align-items: center;

            justify-content: space-evenly;
            position: relative;


            .blur-top {
                position: absolute;
                z-index: 100;
                top: 0px;
                background: linear-gradient(0deg, rgba(255,255,255,0) 46%, rgba(255,255,255,1) 100%);
                pointer-events: none;
                height: 50px;
                width: 100%;
                
            }


            .blur-bottom {
                position: absolute;
                bottom: 0;
   
                
                background: linear-gradient(0deg, $white 46%, rgba(255,255,255,0) 100%);
                height: 80px;
                width: 100%;
            }
         

            & > div {
                height: calc(100% - 150px);
                width: 100%;

                position: relative;
                padding-right: 15px;

            }


            .wrapper {
                overflow-y: auto;
                overflow-x: hidden;
                width:100%;
                box-sizing: border-box;
                height: calc(100%);
                
                justify-content: flex-start;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 10px;

                padding: 10px 40px;
                padding-bottom: 25px;
               

                
                
                

                .contrib-div{
                    width: 100%;
                    padding : 0 15px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    border: solid 1px $grey;
                    border-radius: 10px;
                    
                    transition: 0.2s;
                    height: 50px !important;
                    box-sizing: border-box;

                    filter: opacity(90%) !important;
                    cursor: pointer;

                    

                    &:hover{
                        background-color: rgb(243, 243, 243);
                    }
                    p {
                        display: flex;
                        align-items: center;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        height: 100%;
                        
                        

                    }
                    p > svg {
                        font-size: 25px;
                        color: black;

                        
                        
                    }


                }
            }
        }
        
    }
}