@import "color.scss";


.document-preview {
  width: 100%;
  height: 100%;

  &-content {

    width: calc(100% - 30px);

    height: calc(100% - 90px);
    border: none;
    margin: 0;
    padding: 10px 15px;
    padding-bottom: 0;


    iframe,
    div {
      width: 100%;
      height: 100%;

      border: solid 1px $grey;
      border-radius: 3px;

      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      h3,
      svg {
        color: $grey;
        font-weight: 900;
        text-align: center;
        font-size: 28px;
        padding: 0 20px;
      }

      svg {
        font-size: 150px;
      }
    }
  }

  &-footer {
    height: 80px;
    padding: 0 10px;

    margin-top: 0;

    display: flex;
    align-items: center;
    justify-content: space-between;

    p {
      text-align: center;
      margin: 0 5px;
      font-size: 14px;
    }




    &--empty {
      color: $grey;
      width: 100%;
      font-weight: 900;
      text-align: center;
    }
  }

}

.rating {
  display: flex;
  align-items: center;
  p {
    margin: 0 10px;
    font-weight: 600;
    width: 20px;
    text-align: center;
  }
  svg {
    font-size: 28px;
    cursor: pointer;
  }
}