@import "color.scss";

.calendarpanel {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    gap: 20px;

    .card {
        width: 400px;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        h2 {
            font-size: 22px;
            text-align: center;
        }
        svg {
            color: $main;
            font-size: 40px;
        }
    }
}

@media screen and (max-width: 1000px) {
    .card {
        width: calc(100% - 40px) !important;
    }
}