@import "color.scss";

.document {
    height: 100%;
    display: flex;
    justify-content: space-between;
    max-height: 100vh;
    width: 100%;

    &-explorer {
        width: calc(50% + 20px);

        display: flex;
        flex-direction: column;
        align-items: center;

        & > {
            .btn {
                margin-top: 20px;
            }
        }
        &-header {
            display: flex;

            margin-top: 20px;
            background-color: $white;
            border-radius: 15px;
            border: solid 1px $grey;
            p {
                margin: 0;
                padding: 10px;
                border-radius: 15px;
                cursor: pointer;

            }

            &--selected {
                background-color: $main;
                color: white;
            }
        }
    
        &-all {
            padding: 10px;
            margin: 10px 5px;
            width: calc(100% - 30px);
            min-height: calc(100% - 240px);
            max-height: calc(100% - 240px);
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(110px, 1fr));
            grid-gap: 15px;
            align-content: flex-start;
            justify-items: center;
            overflow-y: auto;
            box-sizing: border-box;
            
            .explorer-file {
                display: flex;
                flex-direction: column;
                justify-content: space-evenly;
                align-items: center;
                cursor: pointer;
    
                width: 110px;
                height: 130px;
                background-color: $white;
                border-radius: 15px;
                border: 1px solid $grey;
    
                svg {
                    font-size: 70px;
    
                }
    
                h3 {
                    font-size: 11px;
                    width: 105px;
                    overflow: hidden;
                    text-align: center;
                    font-weight: 500;
                    margin: 0;
                }
    
                &:hover, &--select {
                    border: 2px solid $main;
                    width: 109px;
                }
    
                &-mark {
                    display: flex;
                    align-items: center;


    
                    svg, p  {
                        margin: 0;
                        
                        font-size: 14px;
                    }

                    p {
                        margin-left: 3px;
                    }
                }
            }
        }

    }


    
    &-aside {
        width: 50%;
        height: 100%;

        height: calc(100% - 60px);
        border-left: solid 1px $grey;
        background-color: $white;

        box-sizing: border-box;
    }
}

@media screen and (max-width: 1000px) {
    .document {
        flex-direction: column;
        height: fit-content;
        max-height: fit-content;

        &-explorer {
            height: calc(100vh - 240px);

            box-sizing: border-box;
        }
        & > div {
            width: 100%;
      
        }

        &-aside {
            height: 650px;
        }

    }
}