@import "color.scss";

.topiccard {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 300px;
  min-width: 300px;

  height: 190px;
  overflow: overlay;


  &-title {
    text-align: center;
   

    text-align: center;
    margin: 0;
    margin-top: 5px;
    width: 100%;

    font-size: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
  }

  &-content {
    display: flex;
    align-items: flex-end;
  }

  &-text {
    display: flex;
    flex-direction: column;
    width: 100%;
    
    font-size: 0.9vw;

    &-name {
      display: flex;
      align-items: center;
      
      p {
        padding-right: 5px;
      }

      &-important {
        color: $black !important;
      }
    }

    p {
      margin: 0;
      margin-top: 5px;
      color: grey;

    }

  }

  &-head {
    display: flex;
    justify-content: space-between;

    svg {
      font-size: 23px;
      color: $grey;
    }
  }



  &-user {
    width: 20%;
  }


}
