@import "color.scss";

.conversations{


    background-color: $white;
    //box-shadow: rgba(100, 100, 111, 0.103) 0px 7px 29px 0px;
    
    margin-right: 5px;
    border-radius: 15px;
    padding: 10px;
    padding-right: 5px;
    border: solid 1px $grey;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
 

    form {
        display: flex;
        justify-content: space-between;
        padding: 10px 10px;
        box-sizing: border-box;
        border-bottom: solid 1px $grey;
        margin-bottom: 10px;
        align-items: center;

        select {
            width: 150px;
            height: 30px;
        }

    }

    .wrapper {
        display: flex;
        flex-direction: column;
        overflow-y: auto;
        height: calc(100vh - 190px);
        padding-right: 5px;

    }
}