@import "color.scss";


.message {
    width: calc(100% - 2px);

    box-sizing: border-box;
    box-shadow: rgba(149, 157, 165, 0.109) 0px 8px 14px;


    &--user {
        border: solid 1px $grey !important;   
        
        .message-head {
            //background-color: $main;

            
            

            &::before {
                content: "";
                position: absolute;
                top: 0; 
                left: 0;
                width: 100%; 
                height: 54px;  
                opacity: .25; 
                background-color: $main;
                z-index: -1;
            }


        }

        
    }
    &-user {
        display: flex;
        align-items: center;
        img {
            margin: 0;
            margin-top: 5px;
        }
        p {
            margin: 0;
            text-transform: capitalize;
            margin-left: 10px;
        }
    }
    
    &-head {
        padding: 5px 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: solid 1px $grey;

        &--active {
            background-color: $main;
        }
    }

    &-body {

        padding: 7px;
        min-height: 30px;

        p {
            margin: 5px;
        }
        * {
            font-size: 15px;
        }

        h1, h2, h3 {
            color: $black !important;
        }

        td {
            border: solid 1px grey;
            padding: 5px;
            min-width: 20px;
        }
        pre {
            border: solid 1px $grey;

            code {
                overflow-x: scroll !important;
            }
        }
        
        blockquote {
            border-left: solid 3px grey;
            padding: 10px;
    
            font-style: italic;
            margin: 0;
    
            p {
                margin: 0;
            }
        }

        img {
            max-width: 50% !important;
        }

        figure {
            display: flex;
            margin: 0;
            justify-content: center;
            width: 100% !important;
        }
    }

    &-date {
        display: flex;
        align-items: center;

        
        p {
            color: grey;
            margin: 0;
            font-size: 13.5px;
            margin-left: 10px;
            padding: 5px 0px;
            box-shadow: none !important;
        }
    }

    .crown {
        color: rgb(255, 196, 0);
    }

    .admin {
        color: rgb(255, 51, 0);
    }

    .crown, .admin {
        font-size: 22px;
        margin-left: 5px;
    }

}
