@import "color.scss";

.card-category {
    width: calc(100% - 40px);
    height: 80px;
    max-width: 270px;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    transition: .2s ease-in-out;
    box-shadow: none;
    border: solid 1px $grey;
    box-shadow: 0 2px 10px rgb(0 0 0 / 10%);

    img {
        height: 70px;
    }

    &:hover {
        box-shadow: 0 2px 10px rgb(0 0 0 / 20%);
    }

    &-text {

        width: 100%;
        p {
            text-align: center;
            font-size: 17px;
            margin: 0 5px;
            text-transform: capitalize;
            color: black;
        }
    }

    &--mini {
        height: 35px;
        width: fit-content;
        box-sizing: border-box;
        padding: 0 10px;
        box-shadow: 0 2px 10px rgb(0 0 0 / 7%) !important;
        
        img {
            height: 20px;
            margin-right: 5px;
        }

        &:hover {
            box-shadow: 0 2px 10px rgb(0 0 0 / 15%) !important;
        }
    }
}