@import "color.scss";

.login {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    .error {
        color: red;
        font-size: 18px;
    }

    &-laptop {
        width: 25m;
        height: 25em;

    }
    &-deux, &-laptop {
        margin-right:auto;
        margin:2em;
    }
    form {
        display : flex ;
        flex-direction : row;
        background-color:$white;
        width: 52em;
        height: 30em;
        justify-content: space-around;
        border-radius:15px;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    }
    &-mail {
        display: flex;
        flex-direction:column;
    }
    &-password {
        display: flex;
        flex-direction:column;
    }

    &-mail h2 {
        font-weight : bold ;
    }
    input {
        box-shadow: none;
        border:none;
        border-bottom: 2px solid rgb(173, 173, 173);
        background-color: $white ;
        color : $black ;
        transition: .2s ease-in-out;

        &:focus {
            outline:none;
            border-bottom: 2px solid $main;
        }

    }
    input,button{
        height: 3em;
        margin-top:2em;
    }

    &-arobase {
        display:flex;
        flex-direction: row;
        align-items: center;
    }
    p {
        font-size : 10pt ;
        align-self: end;
        margin-left:1em;
    }

    &-link {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        font-size: 14px;
    }

    &-keep {
        display: flex;
        align-items: center;
        cursor: pointor !important;

        input {
            margin: 0;
            margin-right: 10px;
        }
    }
}