@import "color.scss";

.register-crop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.616);
    backdrop-filter: blur(4px);
    &-button {
      display: flex;
      gap: 20px;
      margin-top: 20px;
    }
}

.ReactCrop__image {
    max-height: calc(80vh - 70px);
    max-width: 80vw;
    background-color: white;
  }
  
  .ReactCrop__crop-selection {
    border-radius: 50%;
  }

  .register-img {
    width: 220px;
    height: 220px;
    border-radius: 50%;

    border: solid 1px #ddd;
  }