@import "color.scss";

.timetablecard {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 150px;

    color: black;
    text-decoration: none;

    h2 {
        color: $main;
        font-size: 18px;
    }

    p, h2 {
        text-transform: capitalize;
    }
    
    svg {
        font-size: 26px;
    }

    box-sizing: border-box;
    height: fit-content;
}