@import "color.scss";

.admin {
    display: flex;
    justify-content: center;
    align-items: center;
    &-list {
        display: flex;
        flex-wrap: wrap;
        gap: 30px;
        justify-content: center;
        .card {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            min-width: 200px;

            h2 {
                text-transform: capitalize;
            }

            svg {
                font-size: 40px;
                color: $main;
            }
        }
    }

}