@import "color.scss";

.adminpanel {
    box-sizing: border-box;
    overflow: hidden;

    .head {
        margin-bottom: 20px;

        .search {
            
            p {
                display: flex;
                align-items: center;
                margin: 0;
            }

            input, select {
                border: grey 1px solid;
                padding: 5px;
            }
        }
    }

    &-container {
        max-height: calc(100vh - 100px);

        overflow: auto;
    }


    table {
        width: 100%;
        table-layout: fixed;
        height: calc(100vh - 100px);
        overflow-y: scroll;

        .btn {
            width: min-content;
            margin: auto;
        }

        th, td {
            text-align: center;
            border: solid 1px $grey;
            padding: 5px;
            text-overflow: ellipsis;
            overflow: hidden;

            input, select {
                width: 100%;
                border: $grey solid 1px !important;
                padding: 5px;
                box-sizing: border-box;
            }
        }

        th {
            background-color: $main;
            color: white;
            text-transform: capitalize;
        }

        thead {
            position: sticky;
            top: 0;
            background-color: $back2 !important;
            margin-top: 2px !important;
        }

        tbody:before {
            line-height:5px;
            content:"-";
            color:white; /* to hide text */
            display:block;
        }


        tbody {
            
            width: 100%;

            overflow-y: auto;    /* Trigger vertical scroll    */
            overflow-x: hidden;  /* Hide the horizontal scroll */
        }
    }
}

