@import "color.scss";

.register-wrapper{
    display: flex;
    /*justify-content: space-between;*/
    flex-wrap: wrap;
    gap: 5px;
    

    .subject-button{
        background-color: rgb(224, 224, 224);

        color: rgb(75, 75, 75);
        
        padding:10px;
        border-radius: 10px;
        cursor: pointer;
        transition: 1s ease-in-out;

        &-active {
            background-color: $main;
            color: white;
        }
    }
}

.register-count {
    align-self: center;
    margin-top: 30px;
    border: solid 2px $main;
    padding: 5px 8px;
    border-radius: 50px;
    font-weight: 700;
    color: $main;
}