@import "color.scss";

.mark {
    width: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;

    p {
        
        text-transform: capitalize;
        color: grey;
        margin: 0 0;
        margin-top: 4px !important;
        font-size: 12px;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 80px;
    }

    b {
        font-weight: 500;
    }


}