@import "color.scss";

.register {
    display: flex;
    justify-content: center;
    align-items: center;

    &-main {
        display: flex ;
        justify-content: space-between;
        align-items: center;
        
        background-color: $white;
        width: 52em;
        height: 30em;
        border-radius:15px;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
        padding: 20px;
        box-sizing: border-box;
    }

    form {
        display: flex;
        flex-direction : column;
        justify-content: space-between;
        align-items: center;

        border-left: 1px solid rgb(214, 214, 214);
        padding-left: 3%;
        width: 62%;
        height: 100%;

        h2 {
            text-align: center;
        }

    }

    &-input {
        display: flex;
        flex-direction : column;
        gap: 2px;
    }

    &-button {
        display: flex;
        gap: 20px;
    }

    input {
        box-shadow: none;
        border:none;
        border-bottom: 2px solid rgb(173, 173, 173);
        background-color: $white ;
        color: $black ;
        transition: .2s ease-in-out;
        padding: 5px 0;
        font-size: 15px;
        width: 300px;

        &:focus {
            outline:none;
            border-bottom: 2px solid $main;
        }

    }

    label {
        color: $main;
        margin-top: 10px;
        font-size: 12px;
    }


    &-figure {
        display: flex;
        flex-direction: column;

        .line {
            width: 3px;
            height: 40px;
            background-color: rgb(199, 199, 199);
            margin-left: 24px; 
            transition: 0.35s ease-in-out;
            transition-delay: 0.15s;

            &-active{
                transition: 0.35s ease-in-out;
                transition-delay: 0;
                background-color: $main;
            }
        }

        span {
            display: flex;
            .cirle {
                border-radius: 50%;
    
                background-color: rgb(199, 199, 199);
                width: 50px;
                height: 50px;
    
                display: flex;
                justify-content: center;
                align-items: center;
    
                color: white;
                font-size: 20px;

                transition: 0.35s ease-in-out;
                transition-delay: 0;
            }

            .active {
                background-color: $main;
                transition-delay: 0.15s;
               
            }

            p {
                color: rgb(199, 199, 199);
                margin-left: 10px;
                transition: 0.35s ease-in-out;
                text-transform: capitalize;
            }

            .p-active{
                   color: rgb(94, 94, 94);
            }
        }

    }

    &-recap {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 7px;

        img {
            width: 180px;
            height: 180px;
            border-radius: 50%;
            border: solid 1px #ddd;
            margin-bottom: 15px;
        }

        p {
            margin: 3px;
            text-transform: capitalize;
        }

        &-mail {
            text-transform: lowercase !important;

            color: $main;
            font-size: 14px;
        }
    }


}