@import "color.scss";

.calendarcard {
    display: flex;
    align-items: center;
    height: 50px;
    margin: 5px;
    padding: 5px;

    padding-right: 10px;
    cursor: pointer;
    transition: .2s ease-in-out;
    border-radius: 15px;
    text-decoration: none !important;
    
    &-hour {
        padding-right: 10px;
        font-size: 22px;
        min-width: 70px;
    }

    &-text {
        padding-left: 10px;
        justify-content: space-evenly;

        &-description {
            font-size: 11px;
            color: grey;
        }

        &-category {
            padding-top: 5px;
            text-transform: capitalize;
            text-overflow: ellipsis;
        }

        p {
            margin: 0;
        }
    }

    &:hover {
        background: $back;
        
    }
}