@import "color.scss";

.aside {
    min-width: 280px;
    width: 30vw;
    height: 100vh;
    border-left: solid 1px $grey;
    background-color: $back2;

    @media (min-width: 1200px) {
        min-width: 22vw;
    }

    .identite {
        padding: 15px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        
        &-picture {
            display: flex;
            margin-bottom: 5px;
        }

        p {
            margin-top: 10px;
            text-transform: capitalize;
            font-weight: 600;
        }

        b {
            color: grey;
            font-weight: 500;
            text-transform: capitalize;
        }
    }

    .link {
        display: flex;
        justify-content: center;
        gap: 10px;
        &-container {
            border-radius: 50%;
            background-color: $white;
            border: solid 1px $grey;
            width: fit-content;
            padding: 16px;
            cursor: pointer;

            svg {
                font-size: 26px;
                color: $grey;
            }

            transition: .2s ease-in-out;

            &:hover, &--selected {
                svg {
                    color: $main;
                }
            }
        }

    }

    h3 {
        font-weight: 500;
        color: $black;
        font-size: 22px;
        margin: 10px 0px;
    }

    & > div {
        padding: 5px 20px;
    }

    .calendar {
        &-title {
            display: flex;
            align-items: center;
            margin: 15px 0;
            text-transform: capitalize;

            h3 {
                margin: 0;
            }
            & > p {
                margin: 0;
                margin-left: 7px;
                color: grey;
                font-size: 14px;
            }


        }

        &-all {
            background-color: $white;
            border-radius: 20px;
            border: solid 1px $grey;
            padding: 5px;
        }

    }

    .activity {
        h3 {
            margin-top: 10px;
        }

        &-content {
            

            &-item {
                display: flex;
                align-items: center;
                margin: 8px 5px;
                cursor: pointer;
                text-decoration: none;
                
                p {
                    font-size: 14px;
                    margin: 0;
                    padding-left: 10px;
                }

                svg {
                    font-size: 22px;

                }

                &:hover {
                    text-decoration: underline;
                    text-decoration-color: $black;
                }
            }
        }

    }
}

.notif {
    $size: 20px;
    position: absolute;
    color: white;
    margin-top: -50px;
    margin-left: 20px;
    background-color: #FF4D4B;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 13px;

    border-radius: 50%;
    width: $size;
    height: $size;
}

@media screen and (max-width: 1000px) {
    .aside {
        display: none;
    }
  }