@import "color.scss";

.editmark {
    display: flex;
    justify-content: center;
    align-items: center;

    main {
        display: flex;
        align-items: center;
        padding: 10px;
        gap: 40px;
        min-width: 80%;
        min-height: 80%;
        max-width: 600px;
        max-height: 600px;
        justify-content: space-around;

        form {
            display: flex;
            flex-direction: column;


            span {
                gap: 20px;
                margin-top: 30px;
                display: flex;
                
            }

            input, select {
                border: solid 1px grey;
                padding: 7px;
            }

            label {
                margin: 10px 0;
            }
        }
    }
}