@import "color.scss";

.options {

    .gridtheme {
        display: flex;
    }
    

    .theme {
        transition: .2s ease-in-out;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        height: fit-content;
        width: 130px;
        height: 130px;

        margin: 10px;
        padding: 15px;
        border-radius: 20px;
        background-color: $white;

        &-circle {
            height: 50px;
            width: 50px;
            
            border: solid 1px $black;
            border-radius: 50%;


        }

        p {
            margin-bottom: 0;
            text-align: center;
            text-transform: capitalize;
        }
    }

    .btn {
        height: 30px;
    }
    
    .gestion {
        display: flex;
        flex-direction: column;
        gap: 20px;
        h2 {
            margin-bottom: 0;
        }
        &-disconnect {
            padding: 5px;
            p {
                margin: 7px;
            }
            svg {

                cursor: pointer;
                font-size: 30px;
            }

            &:hover {
                background-color: white;
            }
        }
    }

}