@import "color.scss";

.nomarks {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    p {
        text-align: center;
    }
    h2 {
        margin-top: 0px;
        margin-bottom: 10px;
        font-size: 30px;
        color: $main;
    }

    form {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;

        input {
            width: 100%;
            max-width: 700px;
            padding: 7px;
            border: solid 1px grey;
            font-size: 16px;
        }
    }

    iframe {
        max-width: 100%;
    }

}
.head {
    display: flex;
    justify-content: space-between;
    align-items: center;

    & > * {
        display: flex;
        gap: 15px;
        align-items: center;
    }
}
.average {
    padding: 5px 10px;
    border: 2px solid $main;
    color: white;
    box-shadow: rgba(100, 100, 111, 0.151) 0px 7px 29px 0px;
    background-color: $main;

    b {
        margin: 0;
    }
}

.marks {

    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px 30px;

    * select {
        padding: 4px 6px;
        font-size: 15px;
        text-transform: capitalize;
    }


    h2 {
        margin-top: 0px;
        margin-bottom: 10px;
        color: $main;
    }

    .wait {
        padding: 15px;
        padding-bottom: 5px;
        height: 170px;
    }

    .final {
        padding: 15px;
        height: calc(100vh - 365px);
        display: grid;
        grid-template-rows: 50px 1fr;
        span {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;

            div {
                display: flex;
                align-items: center;
                gap: 15px;
                

            }
        }

        p {
            margin: 0;
        }

        &-wrapper {
            
            display: flex;
            max-height: calc(100vh - 400px);
            gap: 10px;
            overflow-y: auto;
            width: 100%;
            padding: 0 10px;
            padding-top: 10px;
            box-sizing: border-box;
            flex-flow: row wrap;
            justify-content: flex-start !important;

        }
    }

    .wrapper {
        display: flex;
        overflow-y: auto;
        padding: 10px 10px;
        gap: 10px;
        

    }

}


.formmark {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(4px);
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.349);

    form {
        background-color: white;
        padding: 20px; 
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
        border-radius: 15px;
        display: flex;
        flex-direction: column;
        gap: 15px;
        box-sizing: border-box;
        align-self: center;
        border: solid 1px $grey;
        
        p {
            padding: 8px;
            width: 100%;
            text-align: center;
            box-sizing: border-box;
            text-transform: capitalize;
            font-size: 20px;
            font-weight: 500;
        }

        input {
            border: solid 1px $grey;
            font-size: 18px;
            padding: 5px;

            &:disabled {
                color: $grey;
            }
        }
        

        span {
            margin-top: 20px;
            display: flex;
            gap: 10px;
            justify-content: center;
        }
    }


}